.App {
  /*border: 025px solid rgba(205, 205, 205, 0.9);*/
  max-width: 800px;
  text-align: center;
}

.dark-App{
  /*border: 025px solid rgba(50, 50, 50, 0.9);*/
  background: rgba(0,0,0,0.1);
  box-shadow: 0 1rem 3rem rgba(250,250,250,0.3)!important;
}

.popUpModal{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(255,255,255,.9);
  z-index: 100;
}

.dark-popUpModal{
  background: rgba(0,0,0,.9);
}

#error{
  position:fixed;
  width: 100%;
  top:0;
  z-index: 1000;
}
